import BackendQuery from 'decentralized/BackendQuery.js';
import {NewUnofficialSporeCreated} from 'decentralized/EthereumEvents.js';

export const getSporeRemixes = async (collectionAddress, tokenId) => {
    let query = new BackendQuery("0xD810401bAb352E3D1a75b2799b4D66819C25E225")
        .from(NewUnofficialSporeCreated);

    console.log("Searching for collectionAddress=% tokenId=%s", collectionAddress, tokenId);
    let unfiltered = await query.executeQuery();
    console.log("unfiltered results=", unfiltered);

    if (collectionAddress) {
        query.where("collectionAddress", collectionAddress);
    }
    if (tokenId !== undefined) {
        let parsed = parseInt(tokenId);
        query.where("tokenId", isNaN(parsed) ? tokenId : parsed);
    }

    let results = await query.executeQuery();
    return results;
};

export const getRemixesMap = async () => {
    let query = new BackendQuery("0xD810401bAb352E3D1a75b2799b4D66819C25E225")
        .from(NewUnofficialSporeCreated);

    let unfiltered = await query.executeQuery();

    let collections = {};
    for (let result of unfiltered) {
        let {collectionAddress, tokenId} = result;
        collectionAddress = collectionAddress.toLowerCase();
        if (!collections[collectionAddress]) {
            collections[collectionAddress] = {};
        }
        if (!collections[collectionAddress][tokenId]) {
            collections[collectionAddress][tokenId] = [];
        }
        collections[collectionAddress][tokenId].push(result);

    }
    console.log("collections remixes=", collections);
    return collections;
};


