import React, { useEffect, useCallback, useState} from 'react';
import {isMobile} from 'react-device-detect';
import History from './History.js';
import {connect} from 'react-redux';

const EchoSearchBox = ({
    showLinkInput,
    setShowHistory,
    onJack,
    loadJack,
    link,
    setShowInfo,
    setLink,
    showHistory,
    currentSong,
    setCurrentSong,
    loadHistory,
    latestJacks,
}) => { 
  return (
      <div className={!showLinkInput ? "nft-link hide" : "nft-link"}>
        <input
          onKeyDown={e => e.key === "Enter" ? onJack() : []}
          onClick={e => e.stopPropagation()}
          onFocus={() => {
              setShowHistory(true);
              setShowInfo(false);
          }}
          placeholder={isMobile ? "Search or drop a music NFT Link" : "Search an artist or drop a music NFT link"} value={link} onChange={e => {
              setShowHistory(false);
              setShowInfo(false);
              setLink(e.target.value);
          }}/>
        <div onClick={() => setLink('')} className="clear-btn">x</div>
       <div onClick={() => onJack()} className="pushable jack-btn">
          Remix
        </div>
      </div>);
};

export default EchoSearchBox;
