
import {useTokensOnSale} from 'components/hooks/useTokensOnSale.js';
import React, { useRef, useEffect, useCallback, useState} from 'react';
import {connect} from 'react-redux';
import {KEYON_SONGS} from './History.js';


const getKeyonBPM = async (url) => {
    let song = (await KEYON_SONGS()).find(x => x.metadata.url === url);
    if (!song) {
        return 0;
    }
    return song.bpm;
};

const JackerPlayer = ({locked, jackedAudio, tokenId, setBuyThisSkin, remixToPlay}) => { 
    let ref = useRef();
    let {tokensOnSale} = useTokensOnSale();
    let [loaded, setLoaded] = useState();
    let url = "https://zequencer.mypinata.cloud/ipfs/QmSLrHfCPZFLqDuPzE1ZhnbneY1NViBPPnsmuvDhoJnUVP/?tokenId=40&gallery=true";

    useEffect(() => {
        window.addEventListener("touchstart", touchstart, {passive: false});
        window.addEventListener("touchend", touchend, {passive: false});
        return () => {
            if (ref.current) {
                window.removeEventListener("touchstart", touchstart);
                window.removeEventListener("touchmove", touchend);
            }
        };
    }, [locked, remixToPlay]);

    const touchstart= e => {
        document.documentElement.style.overflow = 'auto';
    };

    const touchend= e => {
        document.documentElement.style.overflow = 'hidden';
    };
    
    useEffect(() => {
        if (locked) {
            return;
            ref.current.contentWindow.postMessage({
                type: "jacker-stop",
                body: 1
            }, "*");
        }
    }, [locked]);

    useEffect(() => {
        if (tokenId && loaded && ref.current) {
            setTimeout(() => {
                ref.current.contentWindow.postMessage({
                    type: "tokenId",
                    body: parseInt(tokenId)
                }, "*");
            }, 1000);
        }

        if (!tokenId && tokensOnSale) {
            let _tokens = tokensOnSale.filter(x => x.askPrice);
            let randomlyChosenTokenId = _tokens[Math.floor(Math.random()*_tokens.length)];
            console.log("ranodmly chosen", randomlyChosenTokenId);
            if (randomlyChosenTokenId) {
                let tokenId = randomlyChosenTokenId.tokenId;
                setTimeout(() => {
                    if (!ref.current) {
                        return;
                    }
                    ref.current.contentWindow.postMessage({
                        type: "tokenId",
                        body: parseInt(tokenId)
                    }, "*");
                }, 1000);
                setBuyThisSkin(tokenId);
                return;
            } else {
            }
            setBuyThisSkin(undefined);
        }
    }, [tokenId, loaded, jackedAudio, tokensOnSale, setBuyThisSkin]);

    useEffect(() => {
        window.addEventListener("message", onReceive, false);
    }, [setLoaded]);

    useEffect(() => {
        if (remixToPlay) {
            setLoaded(false);
        }
    }, [remixToPlay, setLoaded]);

    const onReceive = useCallback((e) => {
        if (e.origin !== "https://zequencer.mypinata.cloud") {
            return;
        }
        if (e.data === "ready for stems") {
            console.log("READY FOR STEMS");
            setLoaded(true);
        }
    }, [setLoaded]);

    useEffect(() => {
        console.log('USEFFECT jacked audio vs loaded', jackedAudio, loaded);
        if (!jackedAudio || !loaded) {
            return;
        }
        console.log("&&&&&&&&&&&&&&&& sending jacked audio=", jackedAudio);
        getKeyonBPM(jackedAudio).then(
            bpm => {
                if (!ref.current) {
                    return;
                }
                console.log("keyon bpm=", bpm);
                ref.current.contentWindow.postMessage({
                    type: "nft-jacker",
                    body: {
                        bpm,
                        samples: {
                            drums: jackedAudio,
                            vocals: jackedAudio,
                            bass: jackedAudio,
                            other: jackedAudio
                        }
                    }
                }, "*");
            }
        );
    }, [jackedAudio, loaded, remixToPlay]);

    useEffect(() => {
        if (loaded) {
            return;
            ref.current.contentWindow.postMessage({
                type: "nft-jacker",
                body: {
                    bpm: 128,
                    samples: {
                        drums: "QmbtStnnXjF5SY3v65C8D4JkHVU6zTVwPjLPF54pDRHXru",
                        vocals: "QmVJwnKYyL4gsVE8ENt7SL9M3Zasx5mqTRrVSSchrqYs2E",
 
                        other: "Qmev5j7vb6Ai2Yvmoy86koyv5G7fHaKNZPbMu77nkKgs3t",
                        
                    }
                }
            }, "*");
        }
    }, [loaded]);

    let className = (loaded && jackedAudio) ? "loaded player" : "player";
    if (tokenId !== 40) {
        className += " keyon-player";
    }
    if (locked || remixToPlay) {
        return '';
    }
    return (
        <div
        >
        <iframe
          className={className}
          width={340}
          height={400}
          ref={ref}
          src={url}
          
            />

        </div>
    );
};

const mapStateToProps = (state, props) => {
  return {
  };
};

const mapDispatchToProps = (dispatch) => {
  return {
  };
};

export default connect(mapStateToProps, mapDispatchToProps)(JackerPlayer);
