import React, { useEffect, useCallback, useState} from 'react';
import FinishedUploading  from './FinishedUploading.js';
import ipfs from 'decentralized/Ipfs.js';
import Modal from 'components/modal/Modal.js';
import UnknownProgressBar from 'components/progressbar/UnknownProgressBar.js';
import {connect} from 'react-redux';
import {getSporeRemixes} from 'spores/getSporeRemixes.js';
import './recording.scss';

const SporeRecorded = ({
    recordedRemix,
    uploadingRemix,
    spTokenId,
    tokenId,
    collectionAddress,
    onRemixSigningCompleted
}) => { 
    if (!recordedRemix && !uploadingRemix) {
        return '';
    }
    return (
        <Modal hide={e => []}>
          {!recordedRemix ?
           <div className="spore-recorded-dialog">
             <div className="spore-header">
               Spores recorded your remix.
             </div>
             <div className="uploading-text">
               Processing your new sound.
             </div>
             <div className="progress-container">
               <UnknownProgressBar width={180}/>
             </div>
           </div> : 
           <FinishedUploading
             spTokenId={spTokenId}
             tokenId={tokenId}
             complete={onRemixSigningCompleted}
             collectionAddress={collectionAddress}
             recordedRemix={recordedRemix}/>
}
        </Modal>
    );
};

const mapStateToProps = (state, props) => {
  return {
  };
};

const mapDispatchToProps = (dispatch) => {
  return {
  };
};

export default connect(mapStateToProps, mapDispatchToProps)(SporeRecorded);
