import React, {useState, useEffect, useCallback} from 'react';
import ipfs from 'decentralized/Ipfs.js';

export const useRemixListener = (clear) => {
    let [recordedRemix, setRecordedRemix] = useState();
    let [uploadingRemix, setUploadingRemix] = useState(window.TEST_REMIX_UPLOAD_FLAG);
    let [remixToPlay, setRemixToPlay] = useState();

    useEffect(() => {
        if (window.TEST_REMIX_UPLOAD_FLAG) {
            setTimeout(() => {
                setRecordedRemix("@mmmmm");
            }, 5000);
        }
    }, []);
    
    const onRemixSigningCompleted = useCallback(() => {
        console.log("on remix signing completed triggered");
        clear();
        setRecordedRemix(undefined);
        setUploadingRemix(undefined);
    }, [setRecordedRemix, setUploadingRemix, clear]);

    useEffect(() => {
        window.addEventListener("message", onMessage, false);
        return () => window.removeEventListener("message", onMessage);
    }, [setRecordedRemix, setUploadingRemix]);

    const onMessage = useCallback(e => {
        let data = e.data;
        if (data.type === "uploadingRecording") {
            setUploadingRemix(true);
        } else if (data.type == "sporeRecorded") {
            let json = {
                sliderChanges: data.body.sliderData,
                modeChanges: data.body.modeData
            };
            ipfs.addJson(json).then(
                hash => {
                    console.log("DATA JSON HASH=", json);
                    setRecordedRemix({
                        recordingHash: data.body.recording,
                        dataHash: hash
                    });
                });
        }
    }, [setRecordedRemix, setUploadingRemix]);

    return {recordedRemix, uploadingRemix, onRemixSigningCompleted, remixToPlay, setRemixToPlay};
};
